<template>
<div id="myApp" :class="typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : ''">
  <main>
    <div class="search-box">
      <input 
        type="text" 
        class="search-bar" 
        placeholder="Search..."
        v-model="query"
        @keypress="fetchWeather">
    </div>
    <div class="loadText" v-if="typeof weather.main == 'undefined' && true">Type City Name</div>
    <div class="weather-wrap" v-if="typeof weather.main != 'undefined' && true">
      <div class="location-box">
        <div class="location">{{weather.name}}, {{weather.sys.country}}</div>
        <div class="date">{{dataBuilder()}}</div>
      </div>
      <div class="weather-box">
        <div class="temp">{{Math.round(weather.main.temp)}}Â°c</div>
        <div class="weather">{{weather.weather[0].main}}</div>
      </div>
    </div>
  </main>
</div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'App',
  data(){
    return {
      api_key: '9865563b8f765f9b44473a9d77c52cd5',
      base_url: 'https://api.openweathermap.org/data/2.5/',
      query: '',
      weather : {}
    }
  },
  created(){
      window.resizeTo(300, 300)
  },
  methods:{
    fetchWeather(e)
    {
      if(e.key == "Enter")
      {
        axios.get(`${this.base_url}weather?q=${this.query}&units=metric&APPID=${this.api_key}`)
        .then(res => {
          this.weather = res.data;
        });
      }
    },
    dataBuilder()
    {
      let d = new Date();
      let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();

      return `${day}, ${date} ${month} ${year}`;
    }
  }
}
</script>

